import React from 'react'
import { S_Button } from '../../buttons/button/Button.style'
import { BorderDiv, ControlsDiv, NavButtonContainer, WS_HeaderNavButton } from './HeaderControls.style'
import icon from '../../../assets/icons/arrow_circle.svg'
import { WS_IconButton } from '../headerLayout/HeaderLayout.style'

interface HeaderControlsProps {
  className?: string
  tabs: HeaderTab[]
  onTabClick: (tabId: string) => void
  isHidden: boolean
  onHiddenStateChange: () => void
}

export interface HeaderTab {
  title: string
  id: string
  active: boolean
}

export const HeaderControls: React.FC<HeaderControlsProps> = (props) => {
  return (
    <div className={props.className}>
      <NavButtonContainer>
        {
          props.tabs.map(tab => (
            <WS_HeaderNavButton
              key={tab.id}
              primary={false}
              label={tab.title}
              active={tab.active}
              onClick={() => props.onTabClick(tab.id)}/>
          ))
        }
      </NavButtonContainer>
      <BorderDiv>
        <ControlsDiv>
          <S_Button label={'Выйти'} primary={true}/>
          <S_Button label={'Совместный доступ'} primary={true}/>
          <S_Button label={'История действий'} primary={true}/>
          <S_Button label={'Администрирование'} primary={true}/>
          {
            props.isHidden
              ? (
              <WS_IconButton
                icon={icon}
                border={false}
                onClick={props.onHiddenStateChange}
                rotate={'180deg'}
              />
                )
              : <></>
          }
        </ControlsDiv>
      </BorderDiv>
    </div>
  )
}
