import React, { useEffect, useState } from 'react'
import { Resizable } from 're-resizable'
import { S_ToolLine } from '../../utils/toolLine/ToolLine.style'
import { ToolLineButton } from '../../utils/toolLine/ToolLine'
import { S_ResizeHandle } from '../../utils/handle/ResizeHandle.style'
import { WrapperSideToolbox } from './SideToolbox.style'

interface SideToolboxProps {
  className?: string
  children?: React.FunctionComponent[]
  hiddenState: SideToolboxState
  changeFirstToolboxState: () => void
  changeSecondToolboxState: () => void
}

export interface SideToolboxState {
  isFirstToolHidden: boolean
  isSecondToolHidden: boolean
}

export const SideToolbox: React.FC<SideToolboxProps> = ({ className, children, hiddenState, ...props }) => {
  const [height, setHeight] = useState<number>(800)

  const toolLineButtons: ToolLineButton[] = [
    {
      id: 'toolLineButton1',
      title: 'Tools 1',
      onClick: props.changeFirstToolboxState
    },
    {
      id: 'toolLineButton2',
      title: 'Tools 2',
      onClick: props.changeSecondToolboxState
    }
  ]

  // Check a comment down below
  const calculateToolboxHeight = () => {
    const container = document.getElementById('side-toolbox-container')
    if (container) {
      setHeight(container.clientHeight)
    }
  }

  useEffect(calculateToolboxHeight, [])
  window.addEventListener('resize', calculateToolboxHeight)

  return (
    <WrapperSideToolbox>
      <S_ToolLine buttons={toolLineButtons}/>
      <div className={className} id={'side-toolbox-container'}>
        {/* Re-resizable library cannot resize a block with
          `display: flex; flex-direction: column;` and height in percents.
          https://github.com/bokuweb/re-resizable/issues/442
          So, on component mount get the parent height and calculate an actual height of resizable components
          based on the parent height.
          Also add the function `calculateToolboxHeight` as window resize listener.
          // @ts-ignore */}
        {
          !hiddenState.isFirstToolHidden
            ? (
            <Resizable
              defaultSize={{
                width: '100%',
                height: (height / 2).toString() + 'px'
              }}
              minHeight={(height * 0.2).toString() + 'px'}
              maxHeight={(height * 0.8).toString() + 'px'}
              enable={{
                top: false,
                right: false,
                bottom: true,
                left: false,
                topRight: false,
                bottomRight: false,
                bottomLeft: false,
                topLeft: false
              }}
              handleComponent={hiddenState.isSecondToolHidden
                ? {}
                : {
                    bottom: <S_ResizeHandle vertical={false}/>
                  }}
            >
              tools
            </Resizable>
              )
            : <></>
        }
        {
          !hiddenState.isSecondToolHidden
            ? (
            <div style={{ backgroundColor: 'red', flexGrow: 1, paddingTop: '5px' }}>
              More tools
            </div>
              )
            : <></>
        }
      </div>
    </WrapperSideToolbox>
  )
}
