import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState } from '../store/store'
import { WorkspaceNavTab } from '../components/workspace/workspaceNav/WorkspaceNav'

const initialState: WorkspaceNavTab[] = [
  {
    id: 'crossplot',
    title: 'Кроссплот',
    active: true
  },
  {
    id: 'wellDevelopmentMap',
    title: 'Карта состояния разработки',
    active: false
  },
  {
    id: 'floodingAnalysis',
    title: 'Анализ заводнения',
    active: false
  },
  {
    id: 'valuesInterval',
    title: 'Коридор значений',
    active: false
  },
  {
    id: 'shakhmatka',
    title: 'Шахматка',
    active: false
  },
  {
    id: 'wellAnalysisTab',
    title: 'Анализ показателей скважины',
    active: false
  }
]

export const workspaceNavSlice = createSlice({
  name: 'workspaceActiveTab',
  initialState: { value: initialState },
  reducers: {
    setActiveTab: (state: { value: WorkspaceNavTab[] }, action: PayloadAction<string>) => {
      state.value = state.value
        .map(
          tab => (
            tab.id === action.payload
              ? { ...tab, active: true }
              : { ...tab, active: false }
          )
        )
    },
    setTabs: (state, action) => {
      state = action.payload
    }
  }
})

export const selectActiveTab = (state: RootState) => state.workspaceTabs.value.filter(t => t.active)[0]
export const selectTabs = (state: RootState) => state.workspaceTabs.value
export const { setActiveTab, setTabs } = workspaceNavSlice.actions
export default workspaceNavSlice.reducer
