import React, { useEffect, useState } from 'react'
import { HeaderSectionWrapperDiv, HintText } from './HeaderSection.style'
import { ReactChildrenElement } from '../../utils/types'

interface HeaderSectionProps {
  id: string
  className?: string
  children: ReactChildrenElement
  hint?: string
}

export const HeaderSection: React.FC<HeaderSectionProps> = (props) => {
  const [width, setWidth] = useState<number>(200)

  // Calculate a maximum width of children with respect ot flex wrap
  // and set this width to the element
  //
  // This is required because of fit-content gives wrong width with flex wrap
  const setActualWidth = () => {
    const container: HTMLElement = document.getElementById(props.id) as HTMLElement

    let currentHeight: number = 0
    const maxHeight: number = container.offsetHeight
    const maxWidthElements: number[][] = [[]]
    let iter: number = 0

    for (const el of Object.values(container.childNodes) as HTMLElement[]) {
      if (currentHeight + el.offsetHeight <= maxHeight) {
        maxWidthElements[iter].push(el.offsetWidth)
        currentHeight += el.offsetHeight
      } else {
        maxWidthElements.push([el.offsetWidth])
        iter++
        currentHeight = el.offsetHeight
      }
    }

    setWidth(
      maxWidthElements
        .reduce(
          (acc, val) => acc + Math.max(...val)
          , 0
        )
    )
  }

  // Obtain an actual width after render and after resize
  useEffect(setActualWidth, [])
  window.addEventListener('resize', setActualWidth)

  return (
    <HeaderSectionWrapperDiv>
      <div className={props.className} id={props.id} style={{ width: width + 'px' }}>
        {
          props.children
        }
      </div>
      <HintText>
        {props.hint}
      </HintText>
    </HeaderSectionWrapperDiv>
  )
}
