import React from 'react'

interface ButtonProps {
  className?: string
  label: string
  primary?: boolean
  onClick?: () => void;
  active?: boolean
}

export const Button: React.FC<ButtonProps> = (props) => {
  return (
    <button type={'button'}
            onClick={props.onClick}
            className={props.className}>
      {props.label}
    </button>
  )
}
