import React from 'react'
import { W_ButtonWrapper, WS_Button } from './ToolLine.style'

export interface ToolLineButton {
  id: string
  title: string,
  onClick: () => void
}

interface ToolLineProps {
  className?: string
  height?: string
  buttons?: ToolLineButton[]
}

export const ToolLine: React.FC<ToolLineProps> = (props) => {
  return (
    <div className={props.className}>
      <W_ButtonWrapper>
        {
          props.buttons
            ? (
                props.buttons
                  .map(
                    tlButton => (
                  <WS_Button
                    key={tlButton.id}
                    label={tlButton.title}
                    onClick={tlButton.onClick} />
                    )
                  )
              )
            : <></>
        }
      </W_ButtonWrapper>
    </div>
  )
}
