import React, { useState } from 'react'
import { HeaderTab } from '../headerLayout/HeaderLayout'
import { ProjectHeaderTab } from '../HeaderTabs/ProjectHeaderTab'
import { WellHeaderTab } from '../HeaderTabs/WellHeaderTab'
import { S_HeaderLayout } from '../headerLayout/HeaderLayout.style'

interface HeaderProps {}

const headerTabs: HeaderTab[] = [
  {
    title: 'Проект',
    id: 'project_tab'
  },
  {
    title: 'Скважина',
    id: 'well_tab'
  },
  {
    title: 'Карта',
    id: 'map_tab'
  },
  {
    title: 'Моделирование',
    id: 'modeling_tab'
  },
  {
    title: 'Анализ',
    id: 'analysis_tab'
  },
  {
    title: 'Отчеты',
    id: 'reports_tab'
  }
]

export const Header: React.FC<HeaderProps> = (props) => {
  const [isHeaderToolboxHidden, setIsHeaderToolboxHidden] = useState<boolean>(false)

  return (
    <S_HeaderLayout
      isHidden={isHeaderToolboxHidden}
      onHiddenStateChange={() => setIsHeaderToolboxHidden(!isHeaderToolboxHidden)}
      headerTabs={headerTabs}
    >
      <ProjectHeaderTab tabId={'project_tab'}/>
      <WellHeaderTab tabId={'well_tab'}/>
    </S_HeaderLayout>
  )
}
