import React, { useState } from 'react'
import { S_ResizeHandle } from '../../utils/handle/ResizeHandle.style'
import { S_SideToolbox } from '../sideToolbox/SideToolbox.style'
import { Resizable } from 're-resizable'
import { SideToolboxState } from '../sideToolbox/SideToolbox'
import { SideToolboxContainer } from './WorkspaceLayout.style'
import { S_WorkspaceContainer } from '../workspaceContainer/WorkspaceContainer.style'
import { ReactChildrenElement } from '../../utils/types'
import { selectActiveTab } from '../../../features/workspaceNavSlice'
import { WorkspaceNavTab } from '../workspaceNav/WorkspaceNav'
import { useSelector } from 'react-redux'
import { WorkspaceTabProps } from '../workspaceTabs/WorkspaceTab'

interface WorkspaceWrapperProps {
  className?: string
  children: ReactChildrenElement<WorkspaceTabProps>
}

export const WorkspaceLayout: React.FC<WorkspaceWrapperProps> = ({ className, children }) => {
  const activeTab: WorkspaceNavTab = useSelector(selectActiveTab)

  const [sideToolboxHiddenState, setToolboxHiddenState] = useState<SideToolboxState>(
    {
      isFirstToolHidden: false,
      isSecondToolHidden: false
    }
  )

  const isSideToolboxCompletelyHidden = () => (
    sideToolboxHiddenState.isFirstToolHidden &&
    sideToolboxHiddenState.isSecondToolHidden
  )

  const changeFirstSideToolboxState = () => {
    setToolboxHiddenState(
      {
        ...sideToolboxHiddenState,
        isFirstToolHidden: !sideToolboxHiddenState.isFirstToolHidden
      }
    )
  }

  const changeSecondSideToolboxState = () => {
    setToolboxHiddenState(
      {
        ...sideToolboxHiddenState,
        isSecondToolHidden: !sideToolboxHiddenState.isSecondToolHidden
      }
    )
  }

  return (
    <div className={className}>
      <Resizable
        defaultSize={{
          width: isSideToolboxCompletelyHidden() ? 'min-content' : '20%',
          height: '100%'
        }}
        bounds={'parent'}
        maxWidth={isSideToolboxCompletelyHidden() ? 'min-content' : '70%'}
        minWidth={isSideToolboxCompletelyHidden() ? 'min-content' : '10%'}
        enable={{
          top: false,
          right: !isSideToolboxCompletelyHidden(),
          bottom: false,
          left: false,
          topRight: false,
          bottomRight: false,
          bottomLeft: false,
          topLeft: false
        }}
        handleComponent={{
          right: <S_ResizeHandle vertical={true}/>
        }}
        handleStyles={{ right: { right: '-10px' } }}
      >
        <SideToolboxContainer>
          <S_SideToolbox
            hiddenState={sideToolboxHiddenState}
            changeFirstToolboxState={changeFirstSideToolboxState}
            changeSecondToolboxState={changeSecondSideToolboxState}/>
        </SideToolboxContainer>
      </Resizable>
      <S_WorkspaceContainer>
        {
          'length' in children
            ? (children as React.ReactElement<WorkspaceTabProps>[]).filter(tab => tab.props.tabId === activeTab.id)
            : children
        }
      </S_WorkspaceContainer>
    </div>
  )
}
