import React from 'react'
import { ImageContainer } from './IconButton.style'
import chevron from '../../../assets/icons/chevron.svg'

interface IconButtonProps {
  className?: string
  label?: string
  onClick?: () => void;
  icon: string
  dropdown?: boolean
  border?: boolean
  rotate?: string
  large?: boolean
}

export const IconButton: React.FC<IconButtonProps> = (props) => {
  return (
    <button
      className={props.className}
      onClick={props.onClick}>
      <ImageContainer
        rotate={props.rotate}
        size={props.large ? '40px' : '20px'}
        image={props.icon}/>
      {
        props.label
          ? <div>{props.label}</div>
          : <></>
      }

      {
        props.dropdown
          ? <ImageContainer image={chevron} size={'10px'}/>
          : <></>
      }
    </button>
  )
}
