import React from 'react'
import { useSelector } from 'react-redux'
import { selectTabs, setActiveTab } from '../../../features/workspaceNavSlice'
import { useAppDispatch } from '../../../hooks'
import { S_Button } from '../../buttons/button/Button.style'

export interface WorkspaceNavTab {
  id: string,
  title: string,
  active: boolean
}

interface WorkspaceNavProps {
  className?: string
}

export const WorkspaceNav: React.FC<WorkspaceNavProps> = (props) => {
  const tabs: WorkspaceNavTab[] = useSelector(selectTabs)
  const dispatch = useAppDispatch()

  return (
    <div className={props.className}>
      {
        tabs.map(tab => (
          <S_Button
            key={tab.id}
            label={tab.title}
            primary={tab.active}
            onClick={() => dispatch(setActiveTab(tab.id))}
          />
        ))
      }
    </div>
  )
}
