import React from 'react'
import icon from '../../../assets/icons/arrow_circle.svg'
import { useSelector } from 'react-redux'
import { selectActiveTab, setActiveTab } from '../../../features/headerSlice'
import { useAppDispatch } from '../../../hooks'
import { S_HeaderControls } from '../headerControls/HeaderControls.style'
import { ReactChildrenElement } from '../../utils/types'
import { FlexRowDiv, HeaderBodyContainer, HideButtonContainer, WS_IconButton } from './HeaderLayout.style'

export interface HeaderTab {
  title: string,
  id: string
}

export interface HeaderTabComponentProps {
  tabId: string
}

interface HeaderToolboxProps {
  className?: string
  style?: string
  isHidden: boolean
  onHiddenStateChange: () => void
  headerTabs: HeaderTab[]
  children: ReactChildrenElement<HeaderTabComponentProps>
}

export const HeaderLayout: React.FC<HeaderToolboxProps> = ({
  className,
  isHidden,
  onHiddenStateChange,
  headerTabs,
  children
}) => {
  const activeTab = useSelector(selectActiveTab)
  const dispatch = useAppDispatch()

  const renderContent = () => {
    if ('length' in children) {
      return (children as React.ReactElement<HeaderTabComponentProps>[])
        .filter(child => child.props.tabId === activeTab)
    } else if (children.props.tabId === activeTab) {
      return children
    } else return <></>
  }

  return (
    <div className={className}>
      <S_HeaderControls
        isHidden={isHidden}
        onHiddenStateChange={onHiddenStateChange}
        tabs={
          headerTabs.map(tab => ({
            ...tab,
            active: tab.id === activeTab && !isHidden
          })
          )
        }
        /* Change tab and if the header is hidden, reveal it */
        onTabClick={(tabId => {
          dispatch(setActiveTab(tabId))
          if (isHidden) onHiddenStateChange()
        })}
      />
      {
        !isHidden
          ? (
          <FlexRowDiv>
            <HeaderBodyContainer>
              {renderContent()}
            </HeaderBodyContainer>
            <HideButtonContainer>
              <WS_IconButton
                icon={icon}
                border={false}
                onClick={onHiddenStateChange}
              />
            </HideButtonContainer>
          </FlexRowDiv>
            )
          : <></>
      }
    </div>
  )
}
