import styled, { css } from 'styled-components'
import { Button } from './Button'
import { blue2, blue3, blue4, gray1, gray2, white } from '../../../style/colors'

const primaryStyle = css`
  background-color: ${blue2};
  color: white;
  border: none;
`

const secondaryStyle = css`
  background-color: ${white};
  color: black;
  border: none;
`

export const S_Button = styled(Button)`
  height: 25px;
  width: fit-content;
  border-radius: 5px;

  white-space: nowrap;
  
  ${props => props.primary ? primaryStyle : secondaryStyle}

  padding: 0 15px;

  &:hover {
    background-color: ${props => props.primary ? blue3 : gray1}
  }

  &:active {
    background-color: ${props => props.primary ? blue4 : gray2}
  }

`
