import { configureStore } from '@reduxjs/toolkit'
import headerActiveTabReducer from '../features/headerSlice'
import workspaceActiveTabReducer from '../features/workspaceNavSlice'

export const store = configureStore({
  reducer: {
    headerActiveTab: headerActiveTabReducer,
    workspaceTabs: workspaceActiveTabReducer
  }
})

export type RootState = ReturnType<typeof store.getState>

export type AppDispatch = typeof store.dispatch
